import React from 'react';

import './styles/Main.css';
import './styles/html5bp.css';
import './styles/normalize.css';

import SourceSelector from './SourceSelector.js';


async function getFeed(sources) {
  let articleArray = [];

  for (let i = 0; i < sources.length; i++) {
    if (sources[i] === "Krzana Covid") {
      await articleArray.push(await getKrzana());
    } else if (sources[i] === "NYTimes Popular") {
      await articleArray.push(await getNYTimes());
    } else if (sources[i] === "Guardian Coding") {
      await articleArray.push(await getGuardianCoding());
    } else if (sources[i] === "SpaceFlight News") {
      await articleArray.push(await getSpaceFlightNews());
    }
  };

  return new Promise(function (myResolve) {
    myResolve(articleArray);
  });
}

async function getKrzana() {
  const response = await fetch('https://api.krzana.com/v3/publications?channel_ids[]=32739&limit=50');
  const result = await response.json();

  let articles = [];
  let count = 0;
  for (let i = 0; i < result.length; i++) {
    if (count < 4) {
      if ((result[i].thumbnail !== null) && (result[i].type === "Article") && (result[i].origin !== "The Guardian")) {
        const url = result[i].source_url
        const title = result[i].text;
        const tag = result[i].origin;
        const date = result[i].created_at.substring(0, 10);
        const text = result[i].teaser_text;
        const thumbnail = result[i].thumbnail;

        const article = [url, title, tag, date, text, thumbnail];
        articles.push(article);

        count++;
      }
    }
  };
  return new Promise(function (myResolve) {
    const product = ["Krzana Covid", articles];
    myResolve(product);
  });
};

async function getNYTimes() {
  const response = await fetch('https://api.nytimes.com/svc/mostpopular/v2/emailed/1.json?api-key=8jX3EGImSIW8Js6xembfQKaVtBdMChE2');
  const result = await response.json();

  let articles = [];
  for (let i = 0; i < result.results.length; i++) {
    if (i < 4) {
      const url = result.results[i].url;
      const title = result.results[i].title;
      const tag = result.results[i].section;
      const date = result.results[i].published_date;
      const text = result.results[i].abstract;
      const thumbnail = result.results[i].media[0]["media-metadata"][0].url;

      const article = [url, title, tag, date, text, thumbnail];
      articles.push(article);
    }
  };
  return new Promise(function (myResolve) {
    const product = ["NYTimes Popular", articles];
    myResolve(product);
  });
};

async function getGuardianCoding() {
  const response = await fetch('https://content.guardianapis.com/search?show-fields=thumbnail&page-size=30&q=artificial%20intelligence%20computer%20programming%20software%20development&api-key=1b06a9cd-cdd9-415c-b922-0818623b9009');
  const result_temp = await response.json();
  const result = result_temp.response.results
  let articles = [];

  for (let i = 0; i < result.length; i++) {
    if (i < 4) {
      const url = result[i].webUrl;
      const title = result[i].webTitle;
      const tag = result[i].sectionName;
      const date = result[i].webPublicationDate.substring(0, 10);
      const text = "Read the dang article!!! The Guardian does not supply an abstract for a reason...";
      const thumbnail = result[i].fields.thumbnail;
      const article = [url, title, tag, date, text, thumbnail];
      articles.push(article);
    }
  };

  return new Promise(function (myResolve) {
    const product = ["Guardian Coding", articles];
    myResolve(product);
  });
};

async function getSpaceFlightNews() {
  const response = await fetch('https://spaceflightnewsapi.net/api/v2/articles?_limit=50');
  const result = await response.json();
  let articles = [];

  for (let i = 0; i < result.length; i++) {
    if (i < 4) {
      const url = result[i].url;
      const title = result[i].title;
      const tag = result[i].newsSite;
      const date = result[i].publishedAt.substring(0, 10);
      const text = result[i].summary;
      const thumbnail = result[i].imageUrl;

      const article = [url, title, tag, date, text, thumbnail];
      articles.push(article);
    }
  };

  return new Promise(function (myResolve) {
    const product = ["SpaceFlight News", articles];
    myResolve(product);
  });
};

async function getSourceList(feed) {
  let sourceList = [];
  for (let i = 0; i < feed.length; i++) {
    sourceList.push(feed[i][0]);
  }
  return sourceList;
}

async function getCurrentSource(sourceList) {
  const currentSource = sourceList.shift();
  return currentSource;
}

class Main extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: true,
      feed: [],
      sourceList: [],
      currentSource: "",
    }
  }

  async componentDidMount() {
    const sources = this.props.sources;
    console.log("their sources: " + sources);
    const feed = await getFeed(sources);
    const sourceList = await getSourceList(feed);
    const currentSource = await getCurrentSource(sourceList);


    this.setState({ loading: false, feed: feed, sourceList: sourceList, currentSource: currentSource });
    this.update = this.update.bind(this);
  };

  update = (e) => {
    e.preventDefault();

    let sourceList = this.state.sourceList;
    let currentSource = this.state.currentSource;
    sourceList.push(currentSource);
    currentSource = e.target.innerText;
    sourceList = sourceList.filter(s => s !== currentSource);

    this.setState({ sourceList: sourceList, currentSource: currentSource });
  };

  render() {
    return (
      <div>
        {this.state.loading ?
          <div id="popUp" className="loader"></div>
          :

          <header>
            <section className="container">
              <a href="#"><h1>Feedr</h1></a>
              <nav>
                <ul>
                  <li><a href="#">News Source: <span>{this.state.currentSource}</span></a>
                    <SourceSelector sourceList={this.state.sourceList} update={this.update} />
                  </li>
                </ul>
                <section id="search">
                  <input type="text" name="name" value="" />
                  <a href="#"><img src={require('./images/search.png')} alt="" /></a>
                </section>
              </nav>
              <div className="clearfix"></div>
            </section>
          </header>

          // <div>
          //   <ul>{this.state.feed.map((article) => <li>{article}</li>)}</ul>
          // </div>
        }
      </div>
    )
  }
}

export default Main;


