import './RemoveSourceForm.css';

function RemoveSourceForm(props) {
    const sources = props.sources;
    if (sources.length > 0) {
        return (

            <form onSubmit={props.removeSource}>
                <div>
                    {sources.map(source => (
                        <div>
                            <input type="checkbox" id={source} value={source} />
                            <label htmlFor="src">{source}</label>
                        </div>
                    ))}
                </div>
                <input type="submit" value="Remove Source(s)" />    
            </form>
        )
    } else {
        return (
            <p>You have no sources! There are no sources to remove.</p>
        )
    }

}

export default RemoveSourceForm; 